@use '../base/variables' as v;
@use '../base/mixins' as m;

.footer{
    background-color: v.$primario;
    .footer-contenido{
        padding: 5rem;

        @include m.tablet {
            @include m.grid(3, 1rem);
        }
    }
    .copyright{
        margin: 0;
        text-align: center;
        color: v.$blanco;
        padding: 2rem;
    }
}
.footer-box{
    font-family: v.$fuenteCursiva;
    text-align: center;
    margin: 0 0 v.$separacion 0;
    @include m.tablet {
        margin-bottom: 0;
    }
    h3{
        font-family: v.$fuenteCursiva;
        margin: 0 0 1rem 0;
        color: v.$secundario;
    }
    p, a{
        font-size: 2rem;
        margin: 0;
    }
}