@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
    padding: 5rem 0;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    @include m.tablet {
        min-height: 70rem;
    }

    &::before{
        content: '';
        position: absolute;
        background-color: black;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        opacity: 0.6;
    }
}
//header de cada pagina
.contenido-header{
    position: relative;

    h1{
        text-align: center;
        color: v.$blanco;
        text-transform: uppercase;
        margin-top: 5rem;
        @include m.tablet {
            margin-top: 10rem;
        }

    }
}

/*Todos los headers*/
.header-inicio{
    background-image: url(../img/header_inicio.avif);
}
.header-nosotros{
    background-image: url(../img/header_nosotros.jpg);
}
.header-proceso{
    background-image: url(../img/header_proceso.jpg);
}
.header-menu{
